import LikeButton from "./like-button";
import { promises as fs } from 'fs'
import seedrandom from "seedrandom";

const dictionary = await fs.readFile('HSK词典.json', 'utf8')
  .then(JSON.parse)
  .then(entries => entries.filter(entry => entry.simplified.length == 2))


function currentDateSeed() {
  const now = new Date()
  return `${now.getUTCFullYear()}-${now.getUTCMonth()}-${now.getUTCDate()}`
}

/**
 * no-duplicates sampling
 * @param {number} num number of random items to pull from array 
 * @param {Array} array source
 */
function sample(num, array, seed) {
  console.log(`Sampling with seed: ${seed}`)
  const indices = new Set()
  const rng = seedrandom(seed)
  while (indices.size < num) {
    //let randomIndex = Math.floor(seedrandom(seed)() * array.length)
    let randomIndex = Math.floor(rng() * array.length)
    indices.add(randomIndex)
  }

  return Array.from(indices).map(i => array[i])
}

function chooseWords(num, level) {
  let levels = ['old-1', 'new-1', 'old-2', 'new-2', 'old-3', 'new-3']
  let words = sample(num, dictionary, currentDateSeed())
  return words.map(word => word.simplified)
}

function Header({ title }) {
  return <h1>{title ? title : 'Default title'}</h1>;
}

let guest = 1

function HanziTile({ character }) {
  guest++
  return (
    <div className="border p-4 text-4xl flex items-center justify-center">
      {character} {guest}
    </div>
  )
}

function Cup() {
  // Bad: changing a preexisting variable!
  guest = guest + 1;
  return <h2>Tea cup for guest #{guest}</h2>;
}

function TeaSet() {
  return (
    <>
      <Cup />
      <Cup />
      <Cup />
    </>
  );
}


function HanziGrid({ characters }) {
  // I use the index as the key for character tiles here but allegedly you shouldn't do that.
  // it may cause bugs if the tiles are rearranged or removed.
  guest++
  return (
    <div className="grid grid-cols-4 gap-4">
      {characters.map((char, index) => <div key={index}><HanziTile character={char}/></div>)}
    </div>
  )
}

export default async function HomePage() {
  const todaysWords = chooseWords(8)
  const todaysChars = todaysWords.flatMap(word => Array.from(word))
  console.log(`Today's words: ${todaysWords.join(', ')}`)
  console.log(`currentDateSeed: ${currentDateSeed()}`)
  return (
      <div>
        <Header title="ZiMi字谜!" />
        <HanziGrid characters={todaysChars} />
        <LikeButton />
        <TeaSet />
      </div>
  );
}